import _ from 'lodash'

export default function (value, currentLocale, defaultLocale) {
    if (!_.isObject(value)) {
        return {
            text: value,
            locale: currentLocale,
        }
    }

    if (value[currentLocale]) {
        return {
            text: value[currentLocale],
            locale: currentLocale,
        }
    }

    return {
        text: value[defaultLocale] || '',
        locale: defaultLocale,
    }
}
